#logo {
  width: 50px; }

body {
  background-color: #0d2938;
  color: white;
  font-family: Poppins; }

.nav {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between; }
  .nav__logo {
    display: flex;
    align-items: center; }
  .nav__buttons {
    display: flex; }

.hero {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  padding: 0 5%; }
  .hero #scroll {
    width: 5vw;
    margin: 5vw 0; }
  .hero #image {
    position: absolute;
    height: 20%;
    bottom: 0;
    transform: translate(-50%, 0); }

.about {
  padding: 0 5%;
  display: flex;
  background-color: #05222e;
  width: 100%;
  flex-wrap: wrap; }
  .about__title {
    width: 100%;
    text-align: center; }
  .about__content {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center; }
    .about__content aside {
      width: 40%; }
  .about__stackList {
    display: flex;
    flex-wrap: wrap;
    text-transform: uppercase; }
    .about__stackList li {
      margin: 30px 0;
      display: flex;
      flex-direction: column;
      align-items: center; }
  .about__image {
    width: 60px;
    margin: 0 20px 15px 20px; }
  .about #paragraph {
    margin: 15px 0;
    line-height: 150%; }
  .about__links {
    display: flex;
    justify-content: space-evenly; }
    .about__links #link {
      border: 1px solid white;
      color: white;
      text-decoration: none;
      padding: 3px 20px; }

.projects {
  padding: 0 5%; }
  .projects__title {
    text-align: center; }

.contact {
  padding: 0 5%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  background-color: #05222e; }
  .contact__title {
    text-align: center;
    width: 100%; }
  .contact__form {
    display: flex;
    flex-direction: column; }
  .contact .contact__inputField {
    border-bottom: 2px solid #f77f00;
    background-color: #0d2938;
    padding: 10px;
    margin: 10px 0;
    width: 60vw; }
    .contact .contact__inputField input,
    .contact .contact__inputField textarea {
      background-color: transparent;
      border: none;
      color: white;
      resize: none; }
    .contact .contact__inputField input::placeholder,
    .contact .contact__inputField textarea::placeholder {
      color: white;
      opacity: 0.8; }
  .contact button {
    border: 1px solid #f77f00;
    background-color: transparent;
    color: white;
    width: 50%;
    padding: 15px;
    margin: 0 auto; }
  .contact__links {
    width: 100%; }
